"use strict";

import * as d3 from "./d3";

export default class TaxonomyNodeManager {
    constructor(tax_component, r=6){
        this.tree_g = null;
        this.main = tax_component;
        this.t = null;
        this.r = r;
    }
    draw_nodes(visible_nodes, t){
        this.t  = t;
        const node = this.tree_g.selectAll(".node")
            .data(visible_nodes, d=>d.id);

        node
            .attr("class", d => {
                return "node " + (d.children ? " node--internal" : " node--leaf")+ (d.data.loaded ? " loaded" : "")
            })
            .transition(t)
            .attr("transform", d => "translate(" + d.x + "," + d.y + ")")
            .each((d,i,c)=>this.update_node(d, i, c));
        ;

        node.exit().remove();
        const node_e = node
            .enter().append("g")
            .attr("class", d => "node " + (d.children ? " node--internal" : " node--leaf")+ (d.data.loaded ? " loaded" : "") )
            .on("mouseover", (d,i,c) => {
                d3.select("#info_organism").text(`${d.label}${(d.data.taxId)?" - "+d.data.taxId:""}`);
                d3.select(c[i]).selectAll("circle").transition(300)
                    .attr("r", this.r+2);
            })
            .on("mouseout", (d,i,c) => {
                d3.select("#info_organism").text("");
                d3.select(c[i]).selectAll("circle").transition(300)
                    .attr("r", d=>(d.children||d._children)?this.r:d.data.loaded?this.r/2:this.r-2);

            })
            .on("click", d=>{
                if(!d.data.children || d.data.children.length==0){ //Only leaves have taxId attached
                    this.main.dipatcher.call("spaciesRequested",this.main, d.data.taxId);
                }
                if (d.parent) {
                    d.data.expanded = !d.data.expanded;
                    this.main.update_tree(500);
                }
            })
            .on("dblclick", d=>{
                if(!d.data.children || d.data.children.length==0){ //Only leaves have taxId attached
                    this.main.dipatcher.call("spaciesRequested",this.main, d.data.taxId);
                }
                if (d.parent) {
                    this.main.requestAll(d.data);
                }
            })
            .call(d3.drag()
                .subject((d, i, c)=>{
                    const g = d3.select(c[i]),
                        t = g.attr("transform").match(/translate\((.*),(.*)\)/);
                    return {
                        x:Number(t[1]) + Number(g.attr("x")),
                        y:Number(t[2]) + Number(g.attr("y")),
                    };
                })
                .on("drag", (d, i, c)=> {
                    // d3.event.sourceEvent.stopPropagation();
                    if (d.has_loaded_leaves)
                        d3.select(c[i]).attr("transform",
                            d => "translate(" + d3.event.x + "," + d.y + ")"
                        );
                })
                .on("end", (d, i, c) => {
                    if (d.has_loaded_leaves) {
                        const w = this.main.cell_side,
                            dx = (d3.event.x - d.x);// - w/2,
                        let d_col = Math.round(dx / w);
                        move_tree(d, d_col);
                        var t = d3.transition().duration(500);
                        d3.select(c[i]).transition(t).attr("transform",
                            d => "translate(" + d.x + "," + d.y + ")");
                    }
                })
            )
            .each((d,i,c)=>this.draw_node(d,i,c));

        const move_tree = (d, d_col) => {
            if (!d.children) {
                move_leaf(d,d_col);
            } else {
                d.children.sort((a,b)=>d_col>0?b.x-a.x:a.x-b.x);
                for (let child of d.children)
                    move_tree(child,d_col);
            }

        };
        const move_leaf = (d, d_col) => {
            const current_i = this.main.organisms.indexOf(d.data.id),
                current_o = this.main.current_order.indexOf(current_i);
            d_col = current_o+d_col<0?-current_o:d_col;
            if (d_col != 0) {
                const e = this.main.current_order.splice(current_o, 1);
                this.main.current_order.splice(current_o+d_col, 0, e[0]);
                this.main.update_tree(1000);
                this.main.dipatcher.call("changeOrder",this.main, this.main.current_order)
            }
        };




    }
    draw_node(node, i, context) {
        const g =d3.select(context[i]);
        g.append("circle");
        g.attr("transform", d => "translate(" + d.x + "," + d.y + ")scale(0)" )
            .transition(this.t).delay(300)
            .attr("transform", d => "translate(" + d.x + "," + d.y + ")scale(1)" );

        g.append("text")
            .attr("class", "label-leaves")
            .attr("x",-this.r)
            .attr("y",-this.r)
            .style("text-anchor","end")
            .text(d=>d.data.number_of_leaves > 1 ? d.data.number_of_leaves: "");

        g.append("text")
            .attr("class", "label-species")
            .attr("y",this.r+3)
            .attr("x",this.r+3)
            .style("text-anchor","start")
            .style("transform","rotate(-90deg)")
            .text(d=> {
                let label= "";
                if (d.label!="ROOT") {
                    if (d.children || d._children)
                        label = d.label;
                    else {
                        switch (this.main.tax_label_type) {
                            case "id":
                                label = d.data.taxId;
                                break;
                            case "both":
                                label = d.data.taxId+": "+d.label;
                                break;
                        }
                    }
                }
                return label
            });


        g.append("path")
            .attr("class","node-type")
            .style("fill", "white");

        this.update_node(node, i, context);
    }
    update_node(node, i, context) {
        const g =d3.select(context[i]);
        const r = this.r;

        g.selectAll("circle")
            .attr("r", d=>(d.children||d._children)?r:d.data.loaded?r/2:r-2);

        g.selectAll(".node-type")
            .attr("d", (d) =>{
                if (d.data.loaded) return "";
                const s = d3.symbol().size(4*r*r/5);
                if (d.children||d._children) {
                    s.type(d.data.expanded ? d3.symbolTriangle : d3.symbolCross);
                }else{
                    s.type(d3.symbolCircle);
                }
                return s();
            });
        g.selectAll(".label-species")
            .text(d=> {
                let label= "";
                if (d.label!="ROOT") {
                    label = d.label;
                    if (d.data.taxId){
                        switch (this.main.tax_label_type) {
                            case "id":
                                label = d.data.taxId;
                                break;
                            case "both":
                                label = d.data.taxId+": "+d.label;
                                break;
                        }
                    }
                }
                return label
            });
    }
}